import { useForm } from 'antd/es/form/Form';
import React from 'react'
import { LOGIN_ROUTE, REGISTRATION_ROUTE } from '../../utils/consts';
import { RedButton } from '../PageBlocks';
import { FormContainer, FormContent, FormHeader, StyledForm, FormInput, FormLinks, FormLink, InputDescription } from './FormComponents';
import { Form } from 'antd';


function PasswordResetPage() {
    const [form] = useForm();

    const onFormFinish = (values: any) => {
        alert('    ')
    }

    return (
        <FormContainer>
            <FormContent>
                <FormHeader> </FormHeader>
                <StyledForm form={form} style={{ width: '100%' }} onFinish={onFormFinish}>
                    <Form.Item
                        name={'mail'}
                        rules={[{
                            required: true, message: ' '
                        },
                        {
                            type: 'email',
                            message: ' '
                        }]}>
                        <FormInput placeholder='EMAIL' />
                    </Form.Item>
                    <InputDescription> Email,   ,          </InputDescription>
                </StyledForm>

                <RedButton onClick={() => form.submit()} hover></RedButton>
                <FormLinks>
                    <FormLink to={LOGIN_ROUTE}></FormLink>
                    <FormLink to={REGISTRATION_ROUTE}></FormLink>
                </FormLinks>
            </FormContent>
        </FormContainer>
    )
}

export default PasswordResetPage