import React from 'react'
import { FormContainer, FormContent, FormHeader, FormInput, FormLink, FormLinks, StyledForm } from './FormComponents';
import { useForm } from 'antd/es/form/Form';
import { LOGIN_ROUTE, MAIN_ROUTE, PASSWORD_RESET_ROUTE } from '../../utils/consts';
import { RedButton } from '../PageBlocks';
import { Form } from 'antd';

function RegistrationPage() {
    const [form] = useForm();
    const onFormFinish=(values: any)=>{
      alert('   ')
    }
  
    return (
        <FormContainer>
          <FormContent>
          <FormHeader></FormHeader>
            <StyledForm form={form} style={{width: '100%'}} onFinish={onFormFinish}>
            <Form.Item
                name={'name'}
                rules={[{
                    required: true,
                    message: ' ',
                },
                {
                    pattern: /^([-]{1}[-]{1,23}|[A-Z]{1}[a-z]{1,23})$/,
                    message: ' '
                }]}>
                <FormInput placeholder=' ' />
            </Form.Item>
              <Form.Item
                name={'mail'}
                rules={[{
                  required: true, message: ' '
                },
                {
                  type: 'email',
                  message: ' '
                }]}>
                <FormInput placeholder='EMAIL' />
              </Form.Item>
              <Form.Item
                name={'password'}
                rules={[{
                  required: true, message: ' '
                },]}>
                <FormInput placeholder='' type='password' />
              </Form.Item>
            </StyledForm>
            <RedButton onClick={()=>form.submit()} hover></RedButton>
            <FormLinks>
              <FormLink to={LOGIN_ROUTE}></FormLink>
              <FormLink to={PASSWORD_RESET_ROUTE}> </FormLink>
            </FormLinks>
          </FormContent>
        </FormContainer>
    )
}

export default RegistrationPage